import React from 'react';

function MarketList({ title, items, btcPrice, handleSortByName, handleSortByPrice }) {
    return (
        <div className="market-list">
            <h3>{title} : {items.length} 항목</h3>
            <div className="sort-buttons">
                <button onClick={handleSortByName}>이름 정렬</button>
                <button onClick={handleSortByPrice}>가격 정렬</button>
            </div>
            {items.map((item, index) => (
                <div key={index} className={`market-item ${item.priceChangeClass}`}>
                    <div className="market-name">{item.korean_name} ({item.english_name})</div>
                    <div className="market-code">{item.market}</div>
                    <div className="market-price">
                        {item.price ? item.price + (title === 'KRW Market' ? ' 원' : '') : 'Price not available'}
                        {title === 'BTC Market' && <div style={{ fontSize: '10px', color: 'midnightblue' }}>
                            {(btcPrice * item.price).toLocaleString()}
                        </div>}
                    </div>
                </div>
            ))}
        </div>
    );
}

export default MarketList;